.home {
  .homeContent {
    background: linear-gradient(115deg, $color-1 42%, transparent 100%);
    min-height: $minHeight;
    width: 100%;
    min-height: 500px;
    position: relative;
    overflow: hidden;

    &:after {
      content: '';
      position: absolute;
      height: 100%;
      width: inherit;
      background: url('../../media/studyWallPeper.jpg') no-repeat center/cover;
      z-index: -1;
      filter: brightness(50%);
    }

    .content {
      position: absolute;
      top: 20%;
      left: 12%;
      font-family: $font-muli;

      h1 {
        background: 50% 100% / 10% 10% no-repeat radial-gradient(ellipse at bottom, #fff, transparent, transparent);
        -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
        font-size: 5vw;
        font-family: "Source Sans Pro", sans-serif;
        animation: reveal 1000ms ease-in-out forwards 200ms,
          glow 2500ms linear infinite 2000ms;
      }

      h2 {
        font-size: 2.0rem;
        padding: 1.5rem 0 7rem;
        padding-left: 0.3rem;
        color: $white;

      }

      .pdf {
        a {
          font-size: 1.5rem;
          background: $color-4;
          color: $color-1;
          padding: 1rem 2rem;
          border-radius: $borderRadius;
          box-shadow: 0 5px 1px rgb(15, 15, 15);



        }
      }
    }
  }
}

@media screen and (max-width: 850px) {
  .homeContent {
    margin: 0 !important;
  }
}

@media screen and (max-width:580px) {
  .homeContent {
    &:after {
      background: url('../../media/studyWallPeper.jpg') no-repeat 60% 50%/cover !important;
    }

    h1 {
      font-size: 3rem !important;
    }

    h2 {
      font-size: 1.8rem !important;
    }
  }
}

@keyframes reveal {
  80% {
    letter-spacing: 8px;
  }

  100% {
    background-size: 300% 300%;
  }
}

