.knowledges{
    .knowledgesContent {
        min-height: $minHeight;
        width: 100%;
        position: relative;
        overflow: hidden;
        display: grid;
        grid-template-columns: 38% 1fr 1fr;
        grid-template-rows: 70% 30%;
        grid-template-areas:
        "E E L"
        "O O H";

        .languagesFrameworks, .experience, .otherSkills, .hobbies{
            background: $color-1;
            padding: .8rem;
        }
        h3{
            font-size: 1.7rem;
            margin-bottom: 1rem;
            font-family: $font-mont;
            font-weight: 800;
            color: $color-4;
            text-transform: uppercase;
        }
        .languagesFrameworks{
            grid-area: L;
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr;
            grid-template-areas:
            "L"
            "F";

            .languageDisplay{
                grid-area: L;
            }
            .frameworkDisplay{
                grid-area: F;
            }

            h3{
                margin-bottom: .3rem;
            }

            .years{
               font-size: .65rem;
               display: flex;
               justify-content: space-between;
               color: $white;

               span{
                   width: 36%;

                    
                   &:nth-child(4){
                       text-align: right;
                       width: 10%;
                   }
                   &:nth-child(2):before,
                   &:nth-child(3):before,
                   &:nth-child(4):before{
                       content: '';
                       background: rgba($color: #e6e1e1, $alpha: 1.0);
                       position: absolute;
                       height: 146px;
                       width: 1px;
                       transform: translate(9px,19px);
                   
                   }                  
               }
            }
            .languagesList{
                padding-top: .5rem;

                li {
                    color: $color-4;
                }

                .progressBar {
                    height: .4rem;
                    background: transparent;
                    border-radius: 50px;
                    margin-top: .2rem;
                    position: relative;

                    &:after{
                        content: '';
                        position: absolute;
                        background: $color-3;
                        border-radius: 50px;
                        height: .4rem;
                        width: 0;
                        animation: line 2s ease-out forwards;

                        @keyframes line {
                            to{
                                width: 100%;
                            }
                            
                        }
                    }
                }
            }
        }
        .experience {
          
            grid-area: E;
            display: grid;
            grid-template-rows: 10% 1fr 1fr;
            grid-template-areas: 
              "1"
              "2"
              "3"
              "4";
            
            h3 {
              grid-area: 1;
            }
            h4 {
              font-size: 1.1rem;
              font-family: $font-muli;
              color: $color-3;
            }
            h5 {
              color: $color-4;
              padding: 0.5rem 0 0.5rem 1rem; 
            }
            p {
              text-align: justify;
              padding: 0 1rem;
              color: $color-4;
            }
            .exp-1 {
              grid-area: 2;
            }
            .exp-2 {
              grid-area: 3;
            }
            .exp-3 {
              grid-area: 3;
            }
            .exp-4 {
              grid-area: 4;
            }
          }
          .otherSkills {
            grid-area: O;

            
      
            .list {
              display: flex;
              width: 70%;
              height: 100%;
              margin: 0 auto;
              justify-content: space-between;
      
              ul {
                height: 70%;
                display: grid;
      
                i {
                  color: $color-3;
                  padding-right: .5rem;
                }
                li{
                    color: $color-4;
                }
              }
            }
          }
          .hobbies {
            grid-area: H;
            padding-left: 1.5rem;
      
            ul {
              height: 80%;
              display: grid;
      
              li {
                  color: $color-4;
                i {
                  position: absolute;
                  color: $color-4;
                }
                span {
                  position: absolute;
                  transform: translateX(2.2rem);
                }
              }
            }
          }
        }
      }
      @media screen and (max-width: 950px){
        .knowledgesContent {
          h3 {
            font-size: 1.2rem !important;
          }
          .languagesList {
            padding-top: .2rem !important;
          }
          p {
            font-size: .8rem;
          }
          .otherSkills, .hobbies {
            font-size: .8rem;
          }
        }
      }
      @media screen and (max-width: 850px){
        .knowledgesContent {
          h4 {
            font-size: .9rem !important;
          }
          h5 {
            font-size: .6rem !important;
            padding: .2rem 1rem !important;
          }
        }
      }
      @media screen and (max-width: 500px){
        .knowledgesContent {
          display: grid !important;
          grid-template-columns: 1fr !important;
          grid-template-rows: 1fr 1fr 180px 180px !important;
          
          .languages {
            grid-area: 1 !important;
          }
          .experience {
            grid-area: 2 !important;
            margin-left: 0 !important;
          }
          .otherSkills {
            grid-area: 3 !important;
            margin-right: 0 !important;
          }
          .hobbies {
            grid-area: 4 !important;
            margin-top: .5rem !important;
          }
        }
      }
    
