.sidebar{
    background: #000;
    font-family: $font-mont;
    font-weight: 500;
    height: 100%;
    min-height: $minHeight;
    width: 550px;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 250px 1fr 108px;
    grid-template-areas: 
    "1"
    "2"
    "3";
}

.id{
    grid-area: 1;
    text-align: center;
    user-select: none;
    position: relative;

    .idContent{
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;

        img {
            border-radius: 50%;
            box-shadow: 0 0 0 4px $white, 0 0 0 8px $color-1, 0 0 0 12px $white;
        }
        h3{
            font-size: 1.3rem;
            text-transform: uppercase;
            letter-spacing: .2rem;
            margin-top: 1rem;
            color: $white;
        }
    }
}

.navigation{
    grid-area: 2;
    padding: 4rem 2.8rem;

    ul{
        height: 80%;
        display: grid;

        li{
            padding: 2.8rem 0 1rem 5.7rem;
            position: relative;

            &:hover i {
                transform: translate(3px, 3px);
            }

            a{
                color: #bbb;
            }

            .navActive {
                color: $white;
            }

            i{
                position: absolute;
                transform: translateY(3px);
                transition: .2s ease;

            }
            span{
                margin-left: 1.5rem;       

            }

        }

    }
}

.socialNetwork{
    grid-area: 3;

    ul{
        display: flex;
        width: 100%;
        padding: 0 14px;
        justify-content: space-around;

        li{
            font-size: 1.5rem;
            background: $color-4;
            color: $color-1;
            border-radius: 50%;
            padding: .5rem .8rem;
            transition: 0.3s;
            cursor: pointer;


            a:visited{
                color: $color-1;
            }

            &:hover {
                background: $color-2;
            }
            
        }
    }
    .signature{
        margin-top: 2rem;
        text-align: center;
        letter-spacing: .1rem;
        font-size: .7rem;

        p{
            color: $color-2;

            &:hover{
                color: $color-4;
            }
        }
        
    }
}

@media screen and (max-width : 850px){
    .sidebar{
        display: flex;
        min-height: 50px;
        margin-bottom: .1rem;
        width: 100%;

        .id, .socialNetwork{
            display: none;
        }
        .navigation{
            width: 100%;
            padding: .1rem 1rem;
        }
        ul{
            display: flex;
            justify-content: space-between;
           li{
            padding: 2.2rem 0 2rem .6rem;
           }
        }
        span{
            margin-left: 2rem !important;
        }
    }
}

@media screen and (max-width: 580px){
    .sidebar{
        ul{
            justify-content: space-around !important;

            li{
                padding:  0.4rem 0 !important;
            }
            i{
                font-size: 1.4rem !important;
                transform: translate(-3px) !important;
            }
            span{
                display: none;
            }
        }
    }
}