@import url("https://fonts.googleapis.com/css2?family=Dosis:wght@800&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap");
@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mulish&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");

$font-1: "NovaRound", cursive;
$font-2: "Dosis", sans-serif;
$font-3: "Josefin Sans", serif;
$font-4: "Source Sans Pro";
$font-mont: "Montserrat";
$font-muli: "Mulish";

$color-1: rgb(24, 23, 23);
$color-2: #29455c;
$color-3: #388694;
$color-4: #e9e9e9;
$color-5: rgb(22, 22, 22);
$white: #ddd;
$black: #333;

$borderRadius: 25px;
$shadow: 0 1px 3px #333;
$minHeight: 100vh;

@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center;    
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body {
    max-width: 100%;
    font-family: $font-1;
    color: $black;
}

button {
    cursor: pointer;
}
li {
    list-style-type: none;
}

a{
    text-decoration: none;
    color: $black;
    cursor: pointer;
}

a:visited{
    color: $color-4;
}

.home, .knowledges, .portfolio, .contact {
    display: flex;
    @media screen and (max-width : 850px){   
        display: block !important;     
    }
}

.homeContent, .knowledgesContent, .portfolioContent, .contactContent{



    @media screen and (max-width: 850px){
        margin-left: 0 !important;
    }
}

